import {TextInput} from "./text-input";
import {Validations} from "./../core/validations";
import {InputAddon} from "./input-addon"; 

/**
 * @classdesc Campo utilizado na edição de emails
 *
 * <img src="../../doc_assets/emailinput_example.png" />
 * 
 * @example
 * 
 * import {EmailInput} from "mvcomponents/input";
 * 
 * let emailInput:EmailInput = new EmailInput()
 *     .setLabel("Login:")
 *     .setPlaceholder("email@exemplo.com");
 * 
 * this.append(emailInput);
 * 
 * @export
 * @extends {TextInput}
 * @param {string} [p_text=null] Texto do input.
 * @memberof module:mvcomponents/input
 */
export class EmailInput extends TextInput {
	constructor(p_text:string=""){
		super(p_text);

		this.addStyleName("InputEmail")
			.setSize(2)
			.append(new InputAddon()
				.setText("@"));

		this.validator.add(Validations.email());
	}
}
